import Vuex from 'vuex'
import Vue from 'vue'
import mblog from '../data/blogs'
import mfmovie from '../data/movie'

Vue.use(Vuex);

const actions = {
    // Archive 首次挂载时获取 blogs 和 blogsCategories
    get_blogs(content) {
        content.state.blogs = mblog.blogs.sort((a, b) => b['data'] - a['data'])

        const ori_categories = content.state.blogs.map(b => b.categories)
        content.state.blogsCategories = [...new Set(ori_categories)].map(m => {
            return {
                category: m,
                isActived: true
            }
        })
    },
    get_blog_display_now(content, routePath) {
        content.state.blogDisplayNow = routePath
    },
    get_movie(content) {
        content.state.movie = mfmovie.movie.sort((a, b) => b['premiere'] - a['premiere'])

        content.state.movieFilmOrTV = get_movie_info('filmOrTV')
        content.state.movieCountries = get_movie_info('country')
        content.state.movieCategories = get_movie_info('category')

        function get_movie_info(typeToList) {
            const _ori = content.state.movie.map(m => m[typeToList])
            const state_movie = [...new Set(_ori)].map(m => {
                return {
                    [typeToList]: m,
                    isActived: false
                }
            })
            state_movie.unshift({
                [typeToList]: '全部',
                isActived: true
            })
            return state_movie
        }
    },
    changeSelectedType(content, [selected_list_in_name, selected_type]) {
        content.state.movieSelectedType[selected_list_in_name] = selected_type
    },

    scroll_top(content, scroll_top) {
        content.state.scrollTop = scroll_top
    }
}

const mutations= {

}

const state = {
    blogs: [],
    // 保存选择展示的分类，Archive首次挂载时，isActioved 默认为 true
    blogsCategories: [],
    blogDisplayNow: '',

    movie: [],
    movieCountries: [],
    movieFilmOrTV: [],
    movieCategories: [],
    movieSelectedType: {
        filmOrTV: '全部',
        country: '全部',
        category: '全部'
    },
    pageNow: 1,

    scrollTop: null
}

const getters = {
    // 根据 state.blogsCategories 判断展示的内容
    blogsDisplay(state) {
        const categoriesSelected = state.blogsCategories.map(m => {
            if(m.isActived) {
                return m.category
            }
        }).filter(res => res!=undefined)
        
        return state.blogs.map(m => {
            if(categoriesSelected.includes(m.categories)) {
                return m
            }
        }).filter(res => res!=undefined)
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})