<template>
    <div class="archive">
        <div class="left">
            <div class="Forrest-Gump">Life is like a box of chocolates</div>
            <div class="md-categories">
                <div :class="{'md-category': true, 'md-category-actived': category.isActived}" 
                v-for="category in mdCategories" 
                :key="category.category" 
                @click="category.isActived = !category.isActived; upDateCategories()">
                    {{category.category}}
                </div>
                <div :class="{'all-selected': true, 'all-selected-actived': mdAllSelected}" @click="changeAllSelected">
                    <div class="all-selected-ball"></div>
                </div>
            </div>
            <div class="md-search">
                <input class="search-input" type="text" v-model="mdSearch">
                <i class="search-icon fa fa-search"></i>
            </div>
            <div class="md-list">
                <div class="md" v-for="(md, index) in mdSearchList" :key="index" @click="toBlogShow(md.data, md.id)">
                    {{md.data + ' -- ' + md.title}}
                </div>
            </div>
        </div>
        <ul :class="{'tabs': true, 'tabs-short': $store.state.scrollTop > 0}">
            <li class="tab" @click="showTab">
                <i class="fa fa-th-list"></i>
            <br>
            <span>目录</span>
            </li>
        </ul>
        <div class="right">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Blog',
    data() {
        return {
            mdList: [],
            mdCategories: [],
            mdSearch: '',
            mdAllSelected: true
        }
    },
    computed: {
        mdSearchList() {
            let reg = new RegExp(this.mdSearch, 'i')

            return this.mdList.map(m => {
                if(reg.test(m.data + ' -- ' + m.title)) {
                    return m
                }
            }).filter(res => res!=undefined)
        },
        mdCategories_isActived() {
            return this.mdCategories.map(m => m.isActived)
        }
    },
    watch: {
        mdCategories_isActived: {
            immediate: false,
            handler(val) {
                let count = 0
                val.map(m => {
                    if(m) count++
                })
                this.mdAllSelected = count < 6 ? false : true

            }
        }
    },
    methods: {
        toBlogShow(mdData,mdId) {
            let routePath = '/blog/' + mdData + '-' + mdId
            this.$store.dispatch('get_blog_display_now', routePath)
            this.$router.push(routePath)
        },
        upDateCategories() {
            this.mdList = this.$store.getters.blogsDisplay
        },
        showTab() {
            const abc = document.querySelector('.left')
            if(!abc.style.display || abc.style.display === 'none') {
                abc.style.display = 'block'
            }else {
                abc.style.display = 'none'
            }
        },
        changeAllSelected() {
            this.mdAllSelected = !this.mdAllSelected
            this.mdCategories.map(m => {
                m.isActived = this.mdAllSelected
            })
            this.upDateCategories()
        }
    },
    mounted() {
        if(!this.$store.state.blogs.length) {
            this.$store.dispatch('get_blogs')
        }
        this.mdCategories = this.$store.state.blogsCategories
        this.mdList = this.$store.getters.blogsDisplay

        if(this.$store.state.blogDisplayNow) {
            this.$router.push(this.$store.state.blogDisplayNow)
        }
    },
    // beforeCreate() {
        // document.querySelector('body').setAttribute('style', 'height: 100vh')
    // }
}
</script>

<style lang="less">
.archive {
    // height: 100%;
    display: flex;

    .left {
        // border-right: 1px solid rgba(34, 34, 34, 0.2);
        // display: block;
        padding-top: 100px;
        margin-right: 25px;
        width: 250px;
        margin-left: 25px;

        .Forrest-Gump {
            padding-left: 6px;
            font-family: 'LiSu';
            font-weight: bold;
            padding-bottom: 17px;
            color: #222;
        }

        .md-categories {
            .md-category {
                margin: 1px;
                padding: 5px 10px;
                display: inline-block;
                border: 2px solid rgba(34, 34, 34, 0.2);
                border-radius: 10px;
                transition: all .5s ease-in-out;
                @media (any-hover: hover) {
                &:hover {
                    cursor: pointer;
                    border: 2px solid rgba(34, 34, 34, 0.7);
                    }    
                }
            }

            .md-category-actived {
                border: 2px solid rgba(34, 34, 34, 0.7);
                background-color: #222;
                color: #fff;
            }
        }

        .all-selected {
            display: inline-block;
            position: relative;
            top: 5px;
            left: 8px;
            width: 48px;
            height: 24px;
            background-color: rgba(34, 34, 34, 0.2);
            border-radius: 24px;
            transition: all .2s ease-in-out;
            &:hover {
                cursor: pointer;
            }

            .all-selected-ball {
                display: inline-block;
                position: relative;
                width: 20px;
                height: 20px;
                top: 2px;
                left: 2px;
                border-radius: 50%;
                background-color: #fff;
                // border: 2px solid rgba(34, 34, 34, 0.2);
                transition: left .2s ease-in-out;
            }
        }

        .all-selected-actived {
            background-color: #222;

            .all-selected-ball {
            left: 26px;
            }
        }

        .md-search {
            position: relative;
            .search-input {
                width: 180px;
                height: 20px;
                margin-top: 20px;
                padding: 3px 3px 3px 27px;
                margin-left: 5px;
                font-size: 16px;
                // border-radius: 2px;
                font-size: 14px;
            }
    
            .search-icon {
                position: absolute;
                left: 15px;
                top: 28px;
            }
        }

        .md-list {
            margin: 10px 0;
            height: 800px;
            overflow: auto;

            .md {
                padding: 5px 0 5px 5px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                @media (any-hover: hover) {
                    &:hover {
                        cursor: pointer;
                        background-color: rgba(34, 34, 34, 0.1);
                    }
                }
            }
        }
    }

    .tabs {
        transition: all .3s ease-in-out;
        margin: 0;
        padding: 0;
        height: 66px;
        display: none;
        align-items: center;
        position: fixed;
        margin-left: 3%;
        z-index: 9999;
        .tab {
          width: 28px;
          text-align: center;
          list-style: none;
          padding: 0 10px;
          &:hover {
            cursor: pointer;
          }
        }
      }

      .tabs-short {
        height: 40px;
        span {
          display: none;
        }
      }

    .right {
        // height: 100%;
        // overflow-y: scroll;
        flex: 1;
    }
}

@media (max-width: 950px) {
    .archive {
        flex-direction: column;
        .left {
            display: none;
            padding: 10px;
            width: calc(100% - 20px);
            margin: 88px 0 -66px;
            border-top: 2px solid #555;
            border-bottom: 2px solid #555;
            .md-list {
                height: 220px;
            }
        }
        .tabs {
            display: flex;
        }
    }
}

@media (min-width: 950px) {
    .archive {
        .left {
            display: block !important;
        }
    }
}
// @media screen and (max-width: 888px) {
//     .archive-list {
//         width: 100%;
//     }
// }

/* Float */
.hvr-float {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-float:hover, .hvr-float:focus, .hvr-float:active {
  -webkit-transform: translateY(-8px);
  transform: translateY(-8px);
}
</style>